@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form.container{margin-top:12%;width:60%}.form.container .error{background:red;padding:12px 15px;max-width:40%;display:block;text-align:center;margin:0 auto;color:white;border-radius:5px;margin-bottom:1em}.form.container .clean{background:limegreen;padding:12px 15px;max-width:40%;display:block;text-align:center;margin:0 auto;color:white;border-radius:5px;margin-bottom:1em}.form.container .right-side .input-field{width:100%;position:relative}.form.container .right-side .input-field .field-column{position:relative;margin-top:20px;display:flex}.form.container .right-side .input-field .field-column label{margin-left:11px}.form.container .right-side .input-field .field-column input{height:50px;border-radius:12pt;border:none;padding:20px 18px;box-shadow:0px 0px 5px 0px #D4D4D4}.form.container .right-side .input-field .field-column span{position:absolute;display:flex;left:100%;top:-1em}.form.container .right-side .input-field .field-column span ion-icon{position:relative;right:.5em}.form.container .right-side .input-field .field-column span .error-box{padding:1.5em;border-radius:.8em;font-size:10px;font-style:normal;font-weight:bold;color:#6C6C6C;width:19em;box-shadow:0px 0px 5px 0px #D4D4D4;background:white}.form.container .right-side .input-field .field-column .error-message,.form.container .right-side .input-field .field-column .success-message{margin-left:16px;font-style:italic;display:flex;align-items:center}.form.container .right-side .input-field .field-column .error-message{color:red}.form.container .right-side .input-field .field-column .success-message{color:limegreen}.form.container .right-side .input-field .field-column .error-outline{border:solid red 1px}.form.container .right-side .input-field .field-column .success-outline{border:solid limegreen 1px}.form.container .submit-section{display:flex;justify-content:space-between;align-items:center;margin-top:12px}.form.container .submit-section p{font-size:12px;margin:0;margin-left:10px}.form.container .submit-section p span{font-weight:700}.form.container .submit-section button{margin-right:10px}

.App {
  text-align: center;
  display: flex;
  justify-content: space-around;
  background: #ebf0ec;
  height: 100vh;
  font-family: 'Comfortaa', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

